.half-line {
  position: fixed;
  top: 50%;
  left: 0;
  right: 0;
  width: 100%;
  height: 5px;
  border-top: 2px dashed #fff;
  z-index: 100;
}

.sidebar-menu {
  position: fixed;
  right: 0;
  top: 40%;
  width: 120px;
  border: 1px solid pink;
  text-align: right;
  padding-right: 20px;
}

ul.sidebar-menu  {
  list-style-type: none;
}

.sidebar-menu li a {
  color: #000;
  font-weight: bold;
  text-decoration: none;
}

.sidebar-menu li a.active {
  font-size: 1.25em;
  color: #fff;
}